  margin-top: -50px;
}

/* end of home */

.navbar-heading {
  font-size: 1.7rem !important;
  font-weight: bold;
}

.navbar-link {
  font-weight: 700;
  font-size: 0.9rem;
  text-decoration: none;
  color: black;
}

.menu-link {
  text-decoration: none;
  color: black;
}

.vh-100 {
  height: 100vh;
}

.vh-80 {
  height: 80vh;
}

.vh-20 {
  height: 30vh;
}

.about-title {
  font-size: 25px;
  font-weight: bolder;
}

.about-content {
